/* First section */
.hero-section{
    display: flex;
    justify-content: space-around;
}

.hero-section-lhs h1{
    font-size: 48px;
    color: var(--secondaryColor);
    font-weight: var(--primaryFontWeight);
    line-height: 52.8px;
    margin-bottom: 15px;
}

.gold-word{
    color: #c7cb85;
}

.hero-section-lhs p{
    font-size: 32px;
    color: #7e7e7e;
    line-height: 35.2px;
    font-weight: var(--primaryFontWeight);
    margin-bottom: 50px;
}

.hero-section-lhs button{
    padding: 8px 16px 8px 16px;
    color: white;
    background-color: var(--primaryColor);
    border: none;
    border-radius: 5px;
    width: 150px;
    font-weight: var(--secondaryFontWeight);
    font-size: 16px;
    line-height: 24px;
}

.hero-section-img{
    width: 605px;
    height: 474px;
    margin-top: 48px;
}

/* Study section */

.study-section{
    background-color: var(--primaryBackground);
    padding: 120px 0;
}

.study-section-title{
    text-align: center;
    font-size: 40px;
    color: var(--secondaryColor);
    height: 44px;
    padding-bottom: 50px;
    font-weight: var(--primaryFontWeight);
    line-height: 44px; 
}

.study-section-group{
    display: flex;
    justify-content: center;
    gap: 15px;
    Padding: 24px 17px;
}

.study-section-group-parts{
    text-align: center;
    background-color: #C2EECC;
    padding: 50px 0 0 0;
    height: 212px;
    width: 392px;
    border-radius: 16px;
}

.study-section-group-parts-title{
    font-size: 24px;
    color: var(--secondaryColor);
    font-weight: var(--primaryFontWeight);
    margin-bottom: 25px;
    line-height: 26.4px;
}

.study-section-group-parts-info{
    font-size: 20px;
    color: #3f3f3f;
    font-weight: var(--thirdFontWeight);
    line-height: 28px;
}

/* Get started section */

.get-started{
    background-image: url('../../../images/get-started-phone.png');
    background-size: cover;
    height: 522px;
}

.start-btn{
    position: relative;
    top: 410px;
    left: 95px;
    width: 180px;
    height: 44px;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    color: white;
    background-color: var(--primaryColor);
}

/* Reviews section */

.reviews {
   margin-top: 120px;
   background: linear-gradient(#51af4e 75.2% , rgba(16, 35, 16, 0.3012));
   height: 522px;
}

.reviews-h2{
    text-align: center;
    padding: 54px 0;
    font-weight: var(--primaryFontWeight);
    color: var(--secondaryColor);
    font-size: 40px;
    line-height: 44px;
}

.reviews-section{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.reviews-box{
    width: 392px;
    height: 269px;
    border-radius: 8px;
    background-color: #fff;
    padding: 30px 15px 0;
}

.reviews-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.reviews-heading-p-1{
    width: 208px;
    height: 40px;
    font-weight: var(--primaryFontWeight);
    font-size: 20px;
    line-height: 28px;
}

.reviews-img{
    width: 60px;
    height: 60px;
}

.reviews-heading-p-2{
    width: 208px;
    height: 156px;
    font-weight: var(--thirdFontWeight);
    font-size: 14px;
    line-height: 19.6px;
}

/* Footer section */

.footer-section{
    padding: 20px 30px 20px;
    height: 528px;
    background: linear-gradient(to left, #89d043, #12a454);
    display: flex;
    gap: 200px;
    align-items: center;
}

.footer-bottom-section-lhs{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-img{
    padding-right: 20px;
    width: 200px;
    height: 200px;
}

.footer-icons{
    width: 20px;
    height: 19.92px;
    margin-top: 15px;
    color: white;
}

.footer-mr{
    margin-right: 8px;
}

.footer-bottom-section-rhs{
    display: flex;
    gap: 150px;
}

.footer-p-2 {
    width: 117px;
    height: 27px;
    color: #211f42;
    font-weight: var(--primaryFontWeight);
    font-size: 24px;
    line-height: 26.4px;
    font-family: var(--primaryFontFamily);
}

.footer-p-3{
    width: 154px;
    height: 27px;
    margin: 12px;
    font-size: 20px;
    font-weight: var(--thirdFontWeight);
    line-height: 26.4px;
    color: #fff;
    font-family: var(--secondaryFontFamily);
}
